// clab
@mixin mobile {
  @media (max-width: 792px) {
    @content;
  }
}

@mixin notebook {
  @media (max-width: 1352px) {
    @content;
  }
}

// Media queries:
@mixin tablet {
  @media (min-width: 786px) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: 1024px) {
    @content;
  }
}

// Flex mixin
@mixin flexCC {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin flexColSB {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
